import { NextResponse } from "next/server";
import fs from "fs";
import path from "path";

// GET /api/corners/[track] - Returns corner database for track
export async function GET(
  _request: Request,
  { params }: { params: Promise<{ track: string }> }
) {
  const { track } = await params;
  const cornersDir = path.join(process.cwd(), "..", "corners");
  const cornerDbPath = path.join(cornersDir, `${track}.json`);

  if (!fs.existsSync(cornerDbPath)) {
    return NextResponse.json({ error: "Corner database not found" }, { status: 404 });
  }

  const cornerData = JSON.parse(fs.readFileSync(cornerDbPath, "utf-8"));

  return NextResponse.json(cornerData, {
    headers: {
      'Cache-Control': 'public, max-age=31536000, immutable',
    },
  });
}
